import React, { Component } from 'react';
import { Card, Row, Col } from 'antd';
import AOS from 'aos';
import 'aos/dist/aos.css';

class Description extends Component {
    render() {
        AOS.init()
        return (
            
            <div style={{  padding: '30px', textAlign: "center", marginTop: "50px" }}>
                       <div
              data-aos="fade-up"
              data-aos-anchor-placement="top-bottom">   
                <p style={{fontSize: "1.4rem", background: "#ffffffad", padding: "0.6rem", display: "inline-block", borderRadius: "8px" }}>
                    What do I use when develop a site?
                </p>
              </div>  
                <Row gutter={16}>
                    <Col xs={24} md={12} lg={8}>
                    <div data-aos="flip-down" data-aos-duration="1500" data-aos-delay="100">
                        <Card style={{background: "rgba(76, 173, 198, 0.78", color: "snow"}} headStyle={{ color: "snow"}} title="Front-end:" bordered={false}>
                            <p>Framework: ReactJs</p>
                            <p> AntDesign, Bootstrap, React-Strap</p>
                            <p>HTML5, CSS3, JavaScript (ES6)</p>
                        </Card></div>
                    </Col>
                    <Col xs={24} md={12} lg={8}>
                    <div data-aos="flip-down" data-aos-duration="1500" data-aos-delay="500" data-aos-easing="ease-in-out">
                        <Card style={{background: "rgba(16, 18, 25, 0.8)", color: "snow"}} headStyle={{ color: "snow"}} title="Back-end" bordered={false}>
                            <p>NodeJs</p>
                            <p>RESTful</p>
                            <p>Framework: ExpressJS</p>
                        </Card>
                        </div>
                    </Col>
                    <Col xs={24} md={12} lg={8}>
                    <div data-aos="flip-down" data-aos-duration="1500" data-aos-delay="700" data-aos-easing="ease-in-out">
                        <Card style={{background: "#00cb6fed", color: "snow"}} headStyle={{ color: "snow"}} title="More" bordered={false}>
                            <p>Resource control: Git</p>
                            <p>Database: MongoDB (Mongoose, MongoAtlas)</p>
                            <p>LESS, SASS, airBnB style guide, PUGjs</p>
                        </Card>
                        </div>
                    </Col>
                </Row>
            </div>
        );
    }
}

export default Description;